___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: Arial, sans-serif;
  background-color: #f2f2f2;
}
body form {
  margin: 20px;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
}
body form label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
}
body form input[type=text] {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 5px;
  border: none;
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
}
body form input[type=text]:focus {
  outline: none;
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
  -moz-box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
  box-sizing: border-box;
}
body form button[type=submit] {
  background-color: #4CAF50;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
body form button[type=submit]:hover {
  background-color: #3e8e41;
}

#loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #3498db;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
  margin: 0 auto;
  margin-top: 50px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
#error {
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  margin: 20px;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  background-color: orange;
  color: black;
}

#results {
  margin: 20px;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
}
#results h2 {
  margin-top: 0;
  font-size: 24px;
  font-weight: bold;
}
#results ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
#results ul li {
  margin-bottom: 10px;
  font-size: 18px;
}`, "",{"version":3,"sources":["webpack://./src/client/styles/style.scss"],"names":[],"mappings":"AAcA;EACE,8BAAA;EACA,yBAAA;AAbF;AAeE;EACE,YAAA;EACA,aAAA;EACA,sBAAA;EACA,mBAAA;EAhBF,+CAiBsB;EAhBtB,4CAgBsB;EAftB,uCAesB;EAdtB,sBAAA;AAIF;AAYI;EACE,cAAA;EACA,mBAAA;EACA,iBAAA;AAVN;AAaI;EACE,WAAA;EACA,aAAA;EACA,mBAAA;EACA,kBAAA;EACA,YAAA;EA9BJ,8CA+BwB;EA9BxB,2CA8BwB;EA7BxB,sCA6BwB;EA5BxB,sBAAA;AAoBF;AASM;EACE,aAAA;EAjCN,8CAkC0B;EAjC1B,2CAiC0B;EAhC1B,sCAgC0B;EA/B1B,sBAAA;AA2BF;AAQI;EACE,yBA5CU;EA6CV,WAAA;EACA,kBAAA;EACA,YAAA;EACA,kBAAA;EACA,eAAA;AANN;AAQM;EACE,yBAnDU;AA6ClB;;AAYA;EACC,0BAAA;EACA,8BAAA;EACA,kBAAA;EACA,YAAA;EACA,aAAA;EACA,kCAAA;EACA,cAAA;EACA,gBAAA;AATD;;AAYA;EACC;IAAK,uBAAA;EARJ;EASD;IAAO,yBAAA;EANN;AACF;AAQA;EArEE,+CAsEoB;EArEpB,4CAqEoB;EApEpB,uCAoEoB;EAnEpB,sBAAA;EAoEA,YAAA;EACA,aAAA;EACA,sBAAA;EACA,mBAAA;EACA,wBAAA;EACA,YAAA;AAHF;;AAMA;EACE,YAAA;EACA,aAAA;EACA,sBAAA;EACA,mBAAA;EAnFA,+CAoFoB;EAnFpB,4CAmFoB;EAlFpB,uCAkFoB;EAjFpB,sBAAA;AAiFF;AAEE;EACE,aAAA;EACA,eAAA;EACA,iBAAA;AAAJ;AAGE;EACE,gBAAA;EACA,UAAA;EACA,SAAA;AADJ;AAGI;EACE,mBAAA;EACA,eAAA;AADN","sourcesContent":["// Variables\r\n$primary-color: #4CAF50;\r\n$secondary-color: #3e8e41;\r\n\r\n// Mixins\r\n@mixin box-shadow($shadow) {\r\n  -webkit-box-shadow: $shadow;\r\n  -moz-box-shadow: $shadow;\r\n  box-shadow: $shadow;\r\n  box-sizing: border-box;\r\n  \r\n}\r\n\r\n// Base styles\r\nbody {\r\n  font-family: Arial, sans-serif;\r\n  background-color: #f2f2f2;\r\n  \r\n  form {\r\n    margin: 20px;\r\n    padding: 20px;\r\n    background-color: #fff;\r\n    border-radius: 10px;\r\n    @include box-shadow(0 0 10px rgba(0, 0, 0, 0.1));\r\n  \r\n    label {\r\n      display: block;\r\n      margin-bottom: 10px;\r\n      font-weight: bold;\r\n    }\r\n  \r\n    input[type=\"text\"] {\r\n      width: 100%;\r\n      padding: 10px;\r\n      margin-bottom: 20px;\r\n      border-radius: 5px;\r\n      border: none;\r\n      @include box-shadow(0 0 5px rgba(0, 0, 0, 0.1));\r\n      &:focus{\r\n        outline: